.body {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  background-color: #e7e5e5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  background-color: #ffffff;
}

@media (max-width: 767px) {
  .container {
    max-width: 100%;
    width: 320px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 100%;
    width: 768px;
    margin: 0 auto;
    box-shadow: 0 1px 15px rgba(1, 1, 1, 0.2);
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
.main-catalog__wrapper {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .main-catalog__wrapper {
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .main-catalog__wrapper {
    padding: 0;
  }
}

.catalog-title__title,
.content-video__title {
